@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&family=Major+Mono+Display&family=Roboto:wght@100&display=swap);
body {
  margin: 0px;
  padding: 0px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 1% 3%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.horizontalList {
  list-style-type: none;
  display: flex;
  padding: 0;
  margin: 0; }
  .horizontalList li:not(:first-child) {
    margin-left: 5px; }

.iconHref {
  color: inherit;
  text-decoration: none;
  display: flex;
  padding: 0px; }
  .iconHref-icon {
    margin-right: 12px;
    width: 15px; }
  .iconHref-text {
    font-size: 0.9em;
    width: 100%; }

.profilePicture {
  border-radius: 100%;
  box-shadow: 2px 2px 13px 0px #7b7878;
  position: absolute;
  right: 10%;
  bottom: 0%; }

.headerBanner {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%; }
  .headerBanner-details {
    height: 100%;
    border-radius: 20px;
    box-shadow: 2px 2px 4px 0px #323e379c;
    padding: 3% 3%; }
    @media (min-width: 768px) {
      .headerBanner-details {
        display: 70%; } }
  .headerBanner-dpConatiner {
    position: relative;
    display: none;
    height: 30%; }
    @media (min-width: 768px) {
      .headerBanner-dpConatiner {
        display: inherit; } }

.timeLine-logo {
  opacity: 0.4; }

.timeLine-connector--primary {
  flex-grow: 50; }

.timeLine-connector--secondary {
  flex-grow: 1; }

.experience {
  height: 100%;
  padding: 0px 10px; }
  .experience-header {
    display: flex;
    height: 10%; }
  .experience-details {
    height: 90%; }

.experienceDetails {
  background-color: #F2F2F2;
  border-radius: 55px;
  padding: 10px 42px;
  margin: 5px 20px;
  box-shadow: 3px 3px 3px 1px #00000054;
  flex-grow: 1;
  height: 45%; }
  .experienceDetails-achievements {
    overflow: auto;
    list-style: none;
    margin: 15px 0 0;
    padding-left: 5px; }
    .experienceDetails-achievements li {
      margin-bottom: 15px;
      font-weight: bold;
      box-shadow: inset 0px -1px 5px 1px #0a0a0a82;
      padding: 13px 15px 10px;
      border-radius: 10px; }

.skillsProgress {
  max-height: 50px;
  max-width: 50px; }
  @media (min-width: 768px) {
    .skillsProgress {
      max-height: 70px;
      max-width: 70px; } }

.mainParent {
  margin: 1% auto;
  width: 94%;
  height: 100%;
  padding: 10px;
  border-radius: 15px;
  transition: background-color 0.5s ease-out;
  grid-template-areas: "header " "sideBar" "main" "footer";
  grid-template-columns: 1; }
  @media (min-width: 768px) {
    .mainParent {
      grid-template-columns: minmax(0, 1fr) 6fr;
      grid-template-areas: "header header" "sideBar main" "sideBar footer";
      grid-template-rows: minmax(100px, 2fr) minmax(50ch, 100ch) minmax(150px, 1fr); } }
  .mainParent header {
    grid-area: header; }
    .mainParent header h1 {
      margin: 20px 0px;
      font: 3em 'Roboto', sans-serif; }
      @media (min-width: 768px) {
        .mainParent header h1 {
          font-size: 5em; } }
  .mainParent section {
    grid-area: sideBar; }
  .mainParent main {
    grid-area: main;
    font-family: 'Lato', sans-serif;
    font-size: 1em; }
    .mainParent main h2 {
      margin: 15px 0px;
      font: 1.5em 'Major Mono Display', monospace; }
      @media (min-width: 768px) {
        .mainParent main h2 {
          font-size: 3em; } }
    .mainParent main h3 {
      margin: 10px 0px;
      font-style: italic; }
  .mainParent footer {
    grid-area: footer;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-top: 20px; }

